import useNotifiStore from '@cetus/stores/src/notifi'
import { Types } from '@notifi-network/notifi-graphql'

export default function useGetNotifiTargetGroups() {
  const { notifiClient } = useNotifiStore()
  const { setDefaultTargetGroup, setEmailTarget, setTelegramTarget, setSmsTarget, setNotifiLoginLoading } = useNotifiStore()

  // 通用的设置目标信息函数
  const setTargetGroupInfo = (targetGroup: Types.TargetGroupFragmentFragment) => {
    if (targetGroup.emailTargets && targetGroup.emailTargets.length > 0) {
      setEmailTarget(targetGroup.emailTargets.slice(-1)[0])
    }
    if (targetGroup.telegramTargets && targetGroup?.telegramTargets.length > 0) {
      setTelegramTarget(targetGroup.telegramTargets.slice(-1)[0])
    }
    if (targetGroup.smsTargets && targetGroup?.smsTargets.length > 0) {
      setSmsTarget(targetGroup.smsTargets.slice(-1)[0])
    }
  }

  const getNotifiTargetGroups = async (client?: any) => {
    try {
      const targetGroup = client ? await client.getTargetGroups() : await notifiClient.getTargetGroups()
      console.log('🚀🚀🚀 ~ useGetNotifiTargetGroups.ts:8 ~ getNotifiTargetGroups ~ targetGroup:', targetGroup)

      if (targetGroup?.length > 0) {
        const firstTargetGroup = targetGroup[0]
        console.log('🚀🚀🚀 ~ First target group:', firstTargetGroup)

        setTargetGroupInfo(firstTargetGroup)
      }

      setDefaultTargetGroup(targetGroup)
      return targetGroup
    } catch (error) {
      console.error('🚀🚀🚀 ~ useGetNotifiTargetGroups.ts:20 ~ getNotifiTargetGroups ~ error:', error)
      return {}
    }
  }

  const ensureTargetGroup = async (ensureTargetGroupParams: { emailAddress?: string; phoneNumber?: string; telegramId?: string }) => {
    try {
      setNotifiLoginLoading(true)

      const params = { ...ensureTargetGroupParams }

      const defaultTargetGroup: Types.TargetGroupFragmentFragment = await notifiClient.ensureTargetGroup({ name: 'Default', ...params })
      if (defaultTargetGroup?.id) {
        setTargetGroupInfo(defaultTargetGroup)
      }

      setDefaultTargetGroup([defaultTargetGroup])
      setNotifiLoginLoading(false)
    } catch (error) {
      setNotifiLoginLoading(false)
      console.error('🚀🚀🚀 ~ useGetNotifiTargetGroups.ts:45 ~ ensureTargetGroup ~ error:', error)
    }
  }

  return { getNotifiTargetGroups, ensureTargetGroup }
}
