import useNotifiStore from '@cetus/stores/src/notifi'
import { useGlobalToast } from '../../hooks'
import useGetNotifiTargetGroups from './useGetNotifiTargetGroups'

export default function useNotifiAlert() {
  const { notifiClient } = useNotifiStore()
  const { defaultTargetGroup, setNotifiAlerts, setNotifiSourceGroups, setNotifiSources, setIsChecked, setNotifiSubscriptionLoading } =
    useNotifiStore()
  const { showCommonToast } = useGlobalToast()
  const { getNotifiTargetGroups } = useGetNotifiTargetGroups()

  // 公共函数：获取默认目标组
  const getDefaultTargetGroup = async () => {
    let targetGroup = defaultTargetGroup.filter(item => item.name === 'Default')
    if (!targetGroup.length) {
      targetGroup = (await getNotifiTargetGroups()).filter((item: any) => item.name === 'Default')
    }
    return targetGroup[0]
  }

  // 处理通知目标
  const handleAlertSource = alerts => {
    const sourceGroup = []
    const sources = []
    const allSource = []

    alerts.forEach(alert => {
      if (
        (alert.sourceGroup.name.includes('position') || alert.name.includes('Position Range Alerts')) &&
        alert.name !== 'Fusion Source Filter' &&
        alert.name !== 'defaultAlert'
      ) {
        sourceGroup.push(alert.sourceGroup)
        sources.push(...alert.sourceGroup.sources.map(source => ({ ...source, alertID: alert.id })))
      }
      allSource.push(...alert.sourceGroup.sources.map(source => ({ ...source, alertName: alert.name })))
    })

    const hasDefaultSource = allSource.some(item => item.alertName === 'defaultAlert')
    if (!hasDefaultSource) {
      initDefaultAlert()
    }

    return { sourceGroup, sources }
  }

  const getNotifiAlerts = async (client?: any) => {
    try {
      const alerts = client ? await client.getAlerts() : await notifiClient.getAlerts()
      console.log('🚀 ~ getNotifiAlerts ~ alerts:', alerts)

      const { sourceGroup, sources } = handleAlertSource(alerts)
      setNotifiAlerts(alerts)
      setNotifiSourceGroups(sourceGroup)
      setNotifiSources(sources)
      setNotifiSubscriptionLoading(false)
    } catch (error) {
      console.error('🚀 ~ getNotifiAlerts ~ error:', error)
    }
  }

  const deleteNotifiAlerts = async (id: string) => {
    console.log('🚀 ~ deleteNotifiAlerts ~ id:', id)
    try {
      await notifiClient.deleteAlert({ id })
      showCommonToast('Unsubscribed', 'success', "You'll no longer receive alerts when the price goes out of range.")
      getNotifiAlerts()
      setNotifiSubscriptionLoading(false)
    } catch (error) {
      console.error('🚀 ~ deleteNotifiAlerts ~ error:', error)
      showCommonToast('There was an error unsubscribe alert', 'rejected', String(error))
      setNotifiSubscriptionLoading(false)
    }
  }

  const createNotifiAlert = async (posID?: string, posIndex?: string) => {
    try {
      const targetGroup = await getDefaultTargetGroup()
      const createAlertInputs = {
        alerts: [
          {
            fusionEventId: '0192d2ba2cc371e5b6583e43295a3272',
            name: posID && posIndex ? `Position Range Alerts${new Date().getTime()}` : 'defaultAlert',
            targetGroupId: targetGroup.id,
            subscriptionValue: posID && posIndex ? JSON.stringify({ pool_address: posID, position_index: String(posIndex) }) : '*',
            filterOptions: JSON.stringify('{"version":1,"input":{}}')
          }
        ]
      }
      const alerts = await notifiClient.ensureFusionAlerts(createAlertInputs)
      console.log('🚀 ~ createNotifiAlert ~ alerts:', alerts)

      showCommonToast(
        'Subscribed to Alerts!',
        'success',
        'Verify your notification channels to receive alerts when the price goes out of range. To edit your alert settings, click the bell icon in the top nav.'
      )
      getNotifiAlerts()
      // setIsChecked(false)
    } catch (error) {
      console.error('🚀 ~ createNotifiAlert ~ error:', error)
      showCommonToast('There was an error subscribe alert', 'rejected', String(error))
      setIsChecked(false)
      setNotifiSubscriptionLoading(false)
    }
  }

  const initDefaultAlert = async () => {
    try {
      const targetGroup = await getDefaultTargetGroup()
      const createAlertInputs = {
        alerts: [
          {
            fusionEventId: '0192d2ba2cc371e5b6583e43295a3272',
            name: 'defaultAlert',
            targetGroupId: targetGroup.id,
            subscriptionValue: '*',
            filterOptions: JSON.stringify('{"version":1,"input":{}}')
          }
        ]
      }
      await notifiClient.ensureFusionAlerts(createAlertInputs)
      getNotifiAlerts()
    } catch (error) {
      console.error('🚀 ~ initDefaultAlert ~ error:', error)
    }
  }

  return { getNotifiAlerts, createNotifiAlert, deleteNotifiAlerts }
}
